import uniPopup from '@/components/uni-popup/uni-popup.vue';
import selectlist from '@/components/selectFoodsList.vue';
import selecttime from '@/components/selectTime.vue';
import '@/common/linq';
import auth from '@/common/auth';
export default {
	components: {
		selectlist,
		selecttime,
		uniPopup
	},
	data() {
		return {
			type: '',
			fdtype: '',
			height: 0,
			heights: 0,
			currentIndex: 0,
			foodSTop: 0,
			selectStatus: '',
			dataList: [],
			today: 0,
			nextMonth: {},
			mealList: [],
			foodsList: [],
			foodsLists: [],
			selectDateList: [],
			tap: 0,
			setLocalData: [],
			selectD: null
		};
	},
	watch: {},
	onShow() {
		uni.getNetworkType({
			success: function(res) {
				if (res.networkType == 'none') {
					uni.reLaunch({
						url: '/pages/notNetwork/notNetwork'
					});
				}
			}
		});
	},
	onLoad() {
		this.getDatas();
	},
	methods: {
		getDatas() {
			this.selectDateList = [];
			this.heights = Number(uni.getSystemInfoSync().windowHeight) - 140;
			let t = this;
			t.getNewDate();
			t.getNestDate();
			this.$api
				.GetCategoryListAsync()
				.then(res => {
					this.mealList = res.data;
					this.selectStatus = res.data[0].categoryGuid;
				})
				.catch(err => {});
			t.$api
				.GetUseFullDateListAsync()
				.then(res => {
					console.log(res);
					if (res.data.length > 0) {
						new Promise((resolve, reject) => {
							res.data.forEach(item => {
								var y = item.menuDate.split('T')[0].split('-')[0];
								var m = item.menuDate.split('T')[0].split('-')[1];
								var d = item.menuDate.split('T')[0].split('-')[2];
								t.selectDateList.push({
									week: auth.judgeWeek(y, m, d),
									month: m + '-' + d,
									y: y
								});
								t.setLocalData.push({
									y: y,
									m: m,
									d: d,
									isNull: false
								});
							});
							resolve(res.data[0].menuDate);
						}).then(data => {
							t.selectD = data;
							t.getNewDate();
							t.getNestDate();
							//获取商品列表
							this.$api
								.GetCategoryListAsync()
								.then(res => {
									this.mealList = res.data;
									this.selectStatus = res.data[0].categoryGuid;
									this.getFoodsList(t.selectStatus, data);
									this.addFoodsList(data);
								})
								.catch(err => {});
						});
					} else {
						uni.getStorage({
							key: 'selectDate',
							success: function(res) {
								res.data.forEach(sd => {
									sd.forEach(dat => {
										t.selectDateList.push({
											week: auth.judgeWeek(dat.year, dat.month, dat.data),
											month: dat.month + '-' + dat.data,
											y: dat.year
										});
									});
								});
								t.selectD = res.data[0][0].year + '-' + res.data[0][0].month + '-' + res.data[0][0].data;
							}
						});
					}
				})
				.catch(err => {});

			this.height = Number(uni.getSystemInfoSync().windowHeight) - 55;
		},
		togglePopup(type) {
			this.type = type;
		},
		toggleFdPopup(type) {
			this.addFoodsList(this.selectD);
			this.fdtype = type;
		},
		selectDataTap(i, d, y) {
			this.tap = i;
			var t = uni.getStorageSync('thisYear');
			var data = y + '-' + d;
			// console.log(data);
			this.selectD = data;
			this.getFoodsList(this.selectStatus, data);
		},
		getFoodsList(status, data) {
			this.$api
				.GetDisheMaintenanceAsync({
					Date: data,
					CategoryGuid: status
				})
				.then(res => {
					this.foodsList = res.data;
				})
				.catch(err => {});
		},
		saveSetup(fl) {
			console.log(fl);
			var aa = fl
				.where(a => a.isChecked == true)
				.select(item => {
					return item.dishesGuid;
				});
			this.$api
				.AddDisheMaintenanceAsync({
					Date: this.selectD,
					CategoryGuid: this.selectStatus,
					DishesGuidArr: aa
				})
				.then(res => {
					// console.log(res);
					if (res.code == 0) {
						this.fdtype = '';
						// console.log(this.selectD);
						this.setLocalData.push({
							y: this.selectD.split('-')[0],
							m: this.selectD.split('-')[1],
							d: this.selectD.split('-')[2],
							isNull: false
						});
						this.getFoodsList(this.selectStatus, this.selectD);
						this.getDatas();
						this.saveData();
					}
				})
				.catch(err => {});
		},
		checkboxChange(fl) {
			this.$set(fl, 'isChecked', !fl.isChecked);
		},
		// 显示添加菜品列表
		addFoodsList(data) {
			this.$api
				.GetCheckedDisheMaintenanceAsync({
					Date: data,
					CategoryGuid: this.selectStatus
				})
				.then(res => {
					// console.log(res);
					this.foodsLists = res.data;
				})
				.catch(err => {});
		},
		select(index) {
			this.currentIndex = index;
			this.selectStatus = this.mealList[index].categoryGuid;
			this.getFoodsList(this.selectStatus, this.selectD);
		},
		selectDate(d, dt) {
			if (d.data <= dt) {
				return false;
			}
			// isLock
			if (d.isLock) {
				return false;
			}

			this.$set(d, 'isLock', false);
			this.$set(d, 'isSelect', !d.isSelect);
		},
		selectTime() {
			var selectDate = [];
			// this.selectDateList = []
			let t = this;
			this.dataList.forEach(item => {
				var arr = [];
				arr = item.data.filter(d => {
					return d.isSelect == true && !d.isLock;
				});
				if (arr.length > 0) {
					selectDate.push(arr);
				}
			});

			if (selectDate.length == 0) {
				uni.showToast({
					title: '请选择日期',
					icon: 'none',
					mask: true
				});
			} else {
				// console.log(selectDate);
				selectDate.forEach(sd => {
					/* data: 21, month: 6, year: 2019 */
					// console.log(sd);
					sd.forEach(dat => {
						t.selectDateList.push({
							week: auth.judgeWeek(dat.year, dat.month, dat.data),
							month: dat.month + '-' + dat.data,
							y: dat.year
						});
					});
				});

				uni.setStorage({
					key: 'selectDate',
					data: selectDate,
					success: function() {
						// console.log('success');
						t.type = '';
						t.selectD = t.selectDateList[0].y + '-' + t.selectDateList[0].month;
						// console.log(t.selectDateList[0])
					}
				});
			}
		},
		// 获取当月的天数
		getNewDate(data) {
			var t = uni.getStorageSync('thisYear');
			let y = new Date(data).getFullYear();
			let m = new Date(data).getMonth() + 1;
			let d = new Date(data).getDate();

			this.nextMonth = {
				y: y,
				m: m
			};
			this.today = t.d;
			let dataTime = t.y + '年' + t.m + '月';
			this.saveData(dataTime, this.judgeOneWeek(t.y, t.m), this.computeMonth(t.y, t.m), this.today, t.m, t.y);
		},
		// 获取下月的天数
		getNestDate() {
			this.dataList = [];
			var t = uni.getStorageSync('thisYear');
			let y = t.y;
			let m = t.m + 1;
			if (m > 12) {
				y = t.y + 1;
			}
			let dataTime = y + '年' + m + '月';
			// console.log(y,m);
			this.saveData(dataTime, this.judgeOneWeek(y, m), this.computeMonth(y, m), 0, m, y);
		},
		// 计算当月有几天
		computeMonth(year, month) {
			var d = new Date(year, month, 0);
			return d.getDate();
		},
		// 判断一号是星期几
		judgeOneWeek(y, m) {
			return parseInt(new Date(new Date(y + '/' + m + '/' + 1).getTime()).getDay());
		},
		// 保存天数
		saveData(dataTime, nodata, data, d, m, y) {
			// data
			// debugger
			var arr = [];
			let t = this;
			new Promise((resolve, reject) => {
				for (var i = 0, len = data; i < len; i++) {
					arr.push({
						data: i + 1,
						month: m,
						year: y,
						isLock: false,
						isSelect: false,
						hasFoods: false
					});
				}

				for (var i = 0, len = arr.length; i < len; i++) {
					// console.log(t.setLocalData.length);
					for (var j = 0, jen = t.setLocalData.length; j < jen; j++) {
						// debugger

						if (arr[i].year == t.setLocalData[j].y && arr[i].month == t.setLocalData[j].m && arr[i].data == t.setLocalData[j].d) {
							arr[i].isLock = true;
							arr[i].isSelect = true;
						}
					}
				}
				resolve();
			}).then(() => {
				t.dataList.push({
					dataTime: dataTime,
					nodata: nodata,
					data: arr,
					toDay: d
				});
			});
		}
	}
};
